// MUI
import Card from '@mui/material/Card';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
import { CHAT_AI } from 'src/config-global';
// route
import { paths } from 'src/routes/paths';

// ----------------------------------------------------------------------

export default function ChatAiView() {
  return (
    <>
      <CustomBreadcrumbs
        heading="Chat thông minh AI"
        links={[
          { name: 'Tiện ích' },
          {
            name: 'Chat AI',
            href: paths.home.chat_ai,
          },
          { name: 'Sử dụng' },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Card sx={{ height: '100vh', mx: 3 }}>
        <iframe
          title="Chat AI"
          src={CHAT_AI}
          style={{
            width: '100%',
            height: '100%',
            border: 'none',
          }}
        />
      </Card>
    </>
  );
}
